describe('test for demo controller', function() {
    var scope, controller;
    beforeEach(module('template-project.demoFeature'));

    beforeEach(inject(function($controller, $rootScope) {
        scope = $rootScope.$new();
        $controller('DemoController as Demo', {$scope: scope});
    }));

    it('first controller should be defined', function() {
        console.log(controller);
        expect(controller).toBeDefined();
    });

    it('should have correct text', function() {
        expect(scope.Demo.demoText).toBe('This is text from first controller')
    });
});